<template>
    <div id="app">
        <div class="alertview" v-if="alertFlag">
            <div class="title">
                提示
            </div>
            <div class="content">{{alertcontent}}</div>
            <button class="confirm" @click="handleAlertviewClick">确定</button>
        </div>
        <Header :nickname="nickname" :openId="openId"  :isHistory="isHistory" @rightClick="handRightClick"></Header>
        <router-view :nickname="nickname" :openId="openId" :headimgurl="headimgurl" class="animated"/>
    </div>
</template>

<script>
import Header from './components/eleCom/header'
import axios from 'axios'
import common from './components/common/common.js'
import { MessageBox } from 'mint-ui';

export default {
    name: 'App',
     data(){
            return {
                code: '',
                nickname: '',
                openId: '',
                headimgurl: '',
                isHistory: false,
                show: false,
                alertFlag: false,
                alertcontent: '这是提示信息方式范德萨范德萨发'
            }
        },
        components: {
           Header,
           MessageBox
        },
        created(){
            this.onBusHistroy()

        },
        mounted(){
            this.code = this.$route.query.code
            axios.get(common.baseUrl+'/request', {
                params:{
                    code: this.code
                }
            }).then((res) => {
                if(res.data.errcode){
                    this.alertViewShow('页面授权已过期，请重新进入')
                }
                //拿到用户的昵称和openId
                this.nickname = res.data.nickname
                this.openId = res.data.openid
                this.headimgurl = res.data.headimgurl
            }).catch((err) => {
                console.log(err)
            })

        },
        methods: {
            handRightClick(flag){
                this.isHistory = flag
            },
            onBusHistroy(){
                common.bus.$on('history', (value) => {
                    this.isHistory = value
                })
            },
            handleAlertviewClick(){
                this.alertFlag = false
                window.location.href = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wxd55316e683616eda&redirect_uri=http://www.dadiandesign.com/activity&response_type=code&scope=snsapi_userinfo&state=state123#wechat_redirect'
            },
            alertViewShow(content){
                this.alertcontent = content
                this.alertFlag = true
            }
        }

}
</script>

<style lang="less">
html,body{
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
}

#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    position: relative;
    .alertview{
        width: 500px;
        background: #fff;
        box-shadow: 5px 5px 15px #888888;
        z-index: 200;
        padding: 10px 0 0px;
        border-radius: 15px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        .title{
            height: 60px;
            line-height: 60px;
            font-size: 30px;
            font-weight: bold;
        }
        .content{
            box-sizing: border-box;
            padding: 20px 35px;
            font-size: 25px;
            border-bottom: 1px solid #ccc;
            color: #999;
        }
        .confirm{
            display: inline-block;
            width: 100%;
            height: 100%;
            padding: 0 25px;
            border: 0;
            border-radius: 0 0 15px 15px;
            font-size: 30px;
            color: #26a2ff;
            height: 80px;
        }
    }
}

</style>





